import request from './request'

export const getAllOrders = (params) => {
  return request({
    url: '/orders',
    method: 'get',
    params
  })
}
export const editOrder = (id, data) => {
  return request({
    url: `orders/${id}`,
    method: 'put',
    data
  })
}
export const getOrderDetail = (id) => {
  return request({
    url: `orders/${id}`,
    method: 'get'
  })
}
export const checkLogistics = (id = 1106975712662) => {
  return request({
    url: `/kuaidi/${id}`,
    method: 'get'
  })
}
